.security {
  display: flex;
  flex-direction: column; }
  @media (min-width: 360px) and (max-width: 765px) {
    .security {
      width: 100%;
      margin-left: 20px; } }
  @media (max-width: 566px) {
    .security {
      margin-left: 0; } }
  .security .wrapperBlock {
    display: flex;
    justify-content: space-between;
    width: 100%; }
    @media (max-width: 1254px) {
      .security .wrapperBlock {
        flex-direction: column; } }
    .security .wrapperBlock .wrapperItemBlock {
      width: 47%; }
      @media (max-width: 1254px) {
        .security .wrapperBlock .wrapperItemBlock {
          width: 100%; } }
      .security .wrapperBlock .wrapperItemBlock form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: #213054;
        border-radius: 30px;
        padding: 40px;
        margin-top: 20px;
        height: 90%; }
        @media (max-width: 566px) {
          .security .wrapperBlock .wrapperItemBlock form {
            padding: 20px; } }
        .security .wrapperBlock .wrapperItemBlock form button {
          padding: 20px;
          width: 60%;
          align-self: flex-end;
          margin-top: 40px; }
          @media (min-width: 768px) and (max-width: 1365px) {
            .security .wrapperBlock .wrapperItemBlock form button {
              width: 80%;
              align-self: center;
              margin-top: 20px; } }
          @media (min-width: 360px) and (max-width: 765px) {
            .security .wrapperBlock .wrapperItemBlock form button {
              margin-top: 20px;
              width: 100%; } }
          .security .wrapperBlock .wrapperItemBlock form button span {
            font-size: 18px; }

.sessionTime_block {
  margin-top: 20px;
  align-items: flex-start; }
  .sessionTime_block .authentication_toggleWrapper {
    padding: 40px;
    margin-top: 20px;
    background: #213054;
    border-radius: 30px;
    display: flex;
    justify-content: space-between; }
    .sessionTime_block .authentication_toggleWrapper .div {
      width: 100%; }

h4 {
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  color: var(--white);
  margin: 0; }
