@import '../../assets/style/mixins';

.layoutWrapper {
  display: flex;
  justify-content: center;

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1920px;
    margin: 0 auto;
    width: 100%;
    height: 100%;

    @include onTablet {
      padding: 71px 0 0 64px;
    }
  
    @include onNotebook {
      padding: 71px 0 0 74px;
    }
  
    @include onFull {
      padding: 102px 20px 20px 136px;
    }
  }
}
