.backBtn {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  background: transparent;
  outline: none;
  color: var(--green);
  border: none;
  cursor: pointer;
  padding: 20px 50px; }
  .backBtn:hover {
    color: var(--blueDark); }
  .backBtn:before {
    content: '';
    width: 20px;
    height: 13px;
    display: inline-block;
    background-image: url("../../../assets/icons/iconArrowBack.svg");
    background-repeat: no-repeat;
    margin-right: 8px; }
