:root {
  --background: linear-gradient(180deg, #172342 0%, #172342 100%);
  --white: #FBFBFB;
  --black: #021725;
  --darkBlue: #213054;
  --greyLight: #96A3AB;
  --greyDark: #55626A;
  --gradienBlack: linear-gradient(161.3deg, #015D7B 0%, #052B43 50%, #4F3265 100%);
  --gradientGreen: linear-gradient(161.3deg, #017B7B 0%, #054343 50%, #324065 100%);
  --gradient: linear-gradient(60.16deg, #00B8B0 5.38%, #0795CF 142.65%);
  --yellow: linear-gradient(90deg, #F49A1A 0%, #FFDE44 101.36%);
  --blueDark: #23BCFA;
  --green: #39D2D1;
  --red: #E06A6A;
  --blueLight: #219DD5; }

.structureItemWrapper {
  background: var(--gradienBlack);
  border-radius: 30px;
  padding: 30px 24px; }
  .structureItemWrapper .structureHeader {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .structureItemWrapper .structureHeader span {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: var(--white); }
      @media screen and (max-width: 450px) {
        .structureItemWrapper .structureHeader span {
          font-size: 14px; } }
  .structureItemWrapper .structureList .structureListTitle {
    display: flex;
    justify-content: space-between;
    margin-top: 32px; }
    .structureItemWrapper .structureList .structureListTitle span {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 120%;
      color: var(--blueLight);
      display: flex;
      align-items: center; }
      .structureItemWrapper .structureList .structureListTitle span:after {
        content: '';
        width: 8px;
        height: 8px;
        display: inline-block;
        border-bottom: 1px solid var(--blueLight);
        border-right: 1px solid var(--blueLight);
        margin-left: 6px;
        transform: rotate(45deg);
        margin-bottom: 3px; }
  .structureItemWrapper .structureList .structureCard {
    margin-top: 24px; }
    .structureItemWrapper .structureList .structureCard .structureListCard {
      display: flex;
      justify-content: space-between;
      position: relative;
      padding-bottom: 14px;
      margin-top: 14px; }
      .structureItemWrapper .structureList .structureCard .structureListCard:before {
        content: '';
        width: 100%;
        display: block;
        border-bottom: solid 1px #70DDF1;
        opacity: 0.1;
        position: absolute;
        bottom: 0; }
      .structureItemWrapper .structureList .structureCard .structureListCard:last-child:before {
        display: none; }
      .structureItemWrapper .structureList .structureCard .structureListCard .line {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 120%;
        text-transform: uppercase;
        color: var(--white); }
      .structureItemWrapper .structureList .structureCard .structureListCard .partners {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 120%;
        color: var(--white); }
      .structureItemWrapper .structureList .structureCard .structureListCard .reward {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 120%;
        color: var(--white); }
        .structureItemWrapper .structureList .structureCard .structureListCard .reward span {
          color: var(--blueDark);
          margin-right: 10px; }
