:root {
  --background: linear-gradient(180deg, #172342 0%, #172342 100%);
  --white: #FBFBFB;
  --black: #021725;
  --darkBlue: #213054;
  --greyLight: #96A3AB;
  --greyDark: #55626A;
  --gradienBlack: linear-gradient(161.3deg, #015D7B 0%, #052B43 50%, #4F3265 100%);
  --gradientGreen: linear-gradient(161.3deg, #017B7B 0%, #054343 50%, #324065 100%);
  --gradient: linear-gradient(60.16deg, #00B8B0 5.38%, #0795CF 142.65%);
  --yellow: linear-gradient(90deg, #F49A1A 0%, #FFDE44 101.36%);
  --blueDark: #23BCFA;
  --green: #39D2D1;
  --red: #E06A6A;
  --blueLight: #219DD5; }

.btn {
  background: var(--gradient);
  border-radius: 15px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: var(--white);
  padding: 20px 50px;
  cursor: pointer;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center; }
  .btn:hover {
    background: var(--blueDark); }
  .btn:focus {
    background: var(--blueLight); }
  .btn img {
    margin-right: 8px; }

.btn_disabled {
  opacity: 0.5; }

@media screen and (max-width: 1700px) {
  .btn {
    font-size: 14px; } }
