:root {
  --background: linear-gradient(180deg, #172342 0%, #172342 100%);
  --white: #FBFBFB;
  --black: #021725;
  --darkBlue: #213054;
  --greyLight: #96A3AB;
  --greyDark: #55626A;
  --gradienBlack: linear-gradient(161.3deg, #015D7B 0%, #052B43 50%, #4F3265 100%);
  --gradientGreen: linear-gradient(161.3deg, #017B7B 0%, #054343 50%, #324065 100%);
  --gradient: linear-gradient(60.16deg, #00B8B0 5.38%, #0795CF 142.65%);
  --yellow: linear-gradient(90deg, #F49A1A 0%, #FFDE44 101.36%);
  --blueDark: #23BCFA;
  --green: #39D2D1;
  --red: #E06A6A;
  --blueLight: #219DD5; }

.settingsMenu {
  background: #213054;
  border-radius: 20px;
  color: white;
  height: 750px; }
  @media (max-width: 955px) {
    .settingsMenu {
      width: 94px; } }
  @media (max-width: 565px) {
    .settingsMenu {
      position: absolute;
      z-index: 9;
      top: 5%;
      right: 20px;
      border: solid 0.5px var(--blueLight); } }
  .settingsMenu .settingsMenuItem {
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative; }
    .settingsMenu .settingsMenuItem .item:last-child {
      position: absolute;
      bottom: 0;
      width: 100%; }
    .settingsMenu .settingsMenuItem .item a {
      padding: 25px 31px;
      display: flex;
      align-items: center; }
      .settingsMenu .settingsMenuItem .item a span {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        color: rgba(255, 255, 255, 0.6);
        margin-left: 16px; }
        @media (max-width: 966px) {
          .settingsMenu .settingsMenuItem .item a span {
            display: none; } }
    .settingsMenu .settingsMenuItem .item .active {
      background: rgba(35, 188, 250, 0.15);
      border-radius: 20px; }
      .settingsMenu .settingsMenuItem .item .active:before {
        content: '';
        position: absolute;
        border-color: var(--blue);
        border-style: solid;
        border-width: 0 3px 0 0;
        border-radius: 15px;
        height: 24px;
        right: 0;
        width: 2px; }
      .settingsMenu .settingsMenuItem .item .active .icon div svg path {
        fill-opacity: 1; }
      @media (min-width: 360px) and (max-width: 765px) {
        .settingsMenu .settingsMenuItem .item .active span {
          position: absolute;
          top: 0;
          left: 665%;
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 22px;
          color: var(--white); } }
