.socialNetwork {
  background: #213054;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 40px; }
  @media (max-width: 566px) {
    .socialNetwork {
      width: 100%; } }
  .socialNetwork .socialNetworkForm {
    width: 100%;
    display: flex;
    justify-content: space-between; }
    @media (min-width: 768px) and (max-width: 1365px) {
      .socialNetwork .socialNetworkForm {
        flex-direction: column; } }
    @media (min-width: 360px) and (max-width: 765px) {
      .socialNetwork .socialNetworkForm {
        flex-direction: column; } }
    .socialNetwork .socialNetworkForm .wrapperBlock {
      width: 47%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-self: center; }
      @media (min-width: 768px) and (max-width: 1365px) {
        .socialNetwork .socialNetworkForm .wrapperBlock {
          width: 100%; } }
      @media (min-width: 360px) and (max-width: 765px) {
        .socialNetwork .socialNetworkForm .wrapperBlock {
          width: 100%; } }
  .socialNetwork button {
    margin-top: 40px; }
    @media (min-width: 360px) and (max-width: 765px) {
      .socialNetwork button {
        margin-top: 20px;
        width: 100%; } }
