.personalData {
  display: flex;
  flex-direction: column; }
  .personalData .wrapperSelect label {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: var(--green);
    margin-bottom: 10px; }
  .personalData .wrapperSelect .select {
    border: 2px solid var(--green);
    box-sizing: border-box;
    border-radius: 16px;
    margin-top: 10px; }
  .personalData_aboutMe {
    display: flex;
    justify-content: space-between; }
    @media (min-width: 768px) and (max-width: 1365px) {
      .personalData_aboutMe {
        flex-direction: column; } }
    @media (min-width: 360px) and (max-width: 765px) {
      .personalData_aboutMe {
        flex-direction: column; } }
    .personalData_aboutMe .aboutMe_data {
      background: #213054;
      border-radius: 30px;
      padding: 73px 40px;
      width: 47%;
      display: flex;
      justify-content: space-between; }
      @media (min-width: 768px) and (max-width: 1365px) {
        .personalData_aboutMe .aboutMe_data {
          width: 100%; } }
      @media (min-width: 360px) and (max-width: 765px) {
        .personalData_aboutMe .aboutMe_data {
          width: 100%;
          padding: 73px 20px; } }
      @media (max-width: 565px) {
        .personalData_aboutMe .aboutMe_data {
          flex-direction: column; } }
      .personalData_aboutMe .aboutMe_data .myAvatar {
        display: flex;
        flex-direction: column;
        align-items: center; }
        @media (max-width: 565px) {
          .personalData_aboutMe .aboutMe_data .myAvatar {
            justify-content: space-between;
            flex-direction: row; } }
        .personalData_aboutMe .aboutMe_data .myAvatar img {
          width: 180px;
          height: 180px;
          border-radius: 50%; }
        .personalData_aboutMe .aboutMe_data .myAvatar_settings {
          display: flex;
          flex-direction: column;
          margin-top: 10px; }
          .personalData_aboutMe .aboutMe_data .myAvatar_settings button {
            padding: 9px 42px;
            margin-top: 10px; }
      .personalData_aboutMe .aboutMe_data .myData {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60%; }
        @media (min-width: 360px) and (max-width: 765px) {
          .personalData_aboutMe .aboutMe_data .myData {
            flex-direction: column; } }
        @media (max-width: 565px) {
          .personalData_aboutMe .aboutMe_data .myData {
            width: 100%;
            flex-direction: row; } }
        .personalData_aboutMe .aboutMe_data .myData .myName {
          display: flex;
          flex-direction: column;
          justify-content: center;
          width: 50%; }
          @media (min-width: 360px) and (max-width: 765px) {
            .personalData_aboutMe .aboutMe_data .myData .myName {
              margin-top: 15px; } }
          .personalData_aboutMe .aboutMe_data .myData .myName .title {
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            color: var(--green); }
          .personalData_aboutMe .aboutMe_data .myData .myName .description {
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
            color: var(--white);
