            margin-top: 10px; }
    .personalData_aboutMe .aboutMe_contacts {
      background: #213054;
      border-radius: 30px;
      padding: 50px 40px;
      width: 47%; }
      @media (min-width: 768px) and (max-width: 1365px) {
        .personalData_aboutMe .aboutMe_contacts {
          margin-top: 20px;
          width: 100%; } }
      @media (min-width: 360px) and (max-width: 765px) {
        .personalData_aboutMe .aboutMe_contacts {
          width: 100%;
          margin-top: 20px;
          padding: 50px 20px; } }
      .personalData_aboutMe .aboutMe_contacts button {
        margin-top: 20px;
        width: 100%; }
  .personalData_verification {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #213054;
    border-radius: 30px;
    padding: 40px 0;
    width: 100%;
    margin-top: 40px; }
    .personalData_verification .title {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: var(--green); }
    .personalData_verification .description {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
