:root {
  --background: linear-gradient(180deg, #172342 0%, #172342 100%);
  --white: #FBFBFB;
  --black: #021725;
  --darkBlue: #213054;
  --greyLight: #96A3AB;
  --greyDark: #55626A;
  --gradienBlack: linear-gradient(161.3deg, #015D7B 0%, #052B43 50%, #4F3265 100%);
  --gradientGreen: linear-gradient(161.3deg, #017B7B 0%, #054343 50%, #324065 100%);
  --gradient: linear-gradient(60.16deg, #00B8B0 5.38%, #0795CF 142.65%);
  --yellow: linear-gradient(90deg, #F49A1A 0%, #FFDE44 101.36%);
  --blueDark: #23BCFA;
  --green: #39D2D1;
  --red: #E06A6A;
  --blueLight: #219DD5; }

.registration {
  align-items: flex-end; }
  .registration .registrationForm {
    border-radius: 0 20px 20px 0;
    color: white; }
    .registration .registrationForm .registrationFormWrapper h1 {
      font-style: normal;
      font-weight: 600;
      font-size: 40px;
      line-height: 49px;
      color: var(--white); }
    .registration .registrationForm .registrationFormWrapper span {
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 22px; }
    .registration .registrationForm .registrationFormWrapper form .footerForm {
      justify-content: flex-end; }
    .registration .registrationForm .registrationFormWrapper form .inputsPhoneWrapper {
      display: flex;
      justify-content: space-between; }
      .registration .registrationForm .registrationFormWrapper form .inputsPhoneWrapper div {
        width: 20%; }
      .registration .registrationForm .registrationFormWrapper form .inputsPhoneWrapper input {
        font-style: normal;
        font-weight: 600;
        font-size: 64px;
        line-height: 78px;
        color: #FFFFFF; }
    .registration .registrationForm .registrationFormWrapper form .footerFormFlex {
      justify-content: space-between; }
      .registration .registrationForm .registrationFormWrapper form .footerFormFlex .backBtn {
        display: flex;
        align-items: center;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        color: var(--green);
        background-color: transparent;
        cursor: pointer; }
        .registration .registrationForm .registrationFormWrapper form .footerFormFlex .backBtn .iconArrowBack {
          margin-right: 8px; }
    .registration .registrationForm .registrationFormWrapper form .footerFormCheckBox {
      display: flex;
      margin-top: 25px; }
      .registration .registrationForm .registrationFormWrapper form .footerFormCheckBox label {
        width: fit-content;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 140%;
        margin-left: 15px; }
        .registration .registrationForm .registrationFormWrapper form .footerFormCheckBox label a {
          color: var(--blueDark); }
          .registration .registrationForm .registrationFormWrapper form .footerFormCheckBox label a:hover {
            color: var(--green); }
    .registration .registrationForm .registrationFormWrapper form .checked div {
      border: solid 1px red;
      position: relative; }
      .registration .registrationForm .registrationFormWrapper form .checked div:before {
        content: '';
        width: 16px;
        height: 16px;
        display: block;
        background-image: url("../../assets/icons/iconErrorWarning.svg");
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        left: -25px; }
    .registration .registrationForm .registrationFormWrapper form .referral input {
      color: #23BCFA; }
  .registration .imagesBlock {
    display: flex;
    align-items: flex-end;
    margin: 0 auto;
    padding-bottom: 80px; }
    .registration .imagesBlock svg {
      width: 100%; }
    .registration .imagesBlock .description {
      margin-left: 60px; }
      .registration .imagesBlock .description h1 {
        font-style: normal;
        font-weight: 600;
        font-size: 64px;
        line-height: 78px;
        color: var(--white);
        margin: 0; }
      .registration .imagesBlock .description a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        color: var(--green);
        display: flex;
        align-items: center;
        margin-top: 30px; }
        .registration .imagesBlock .description a div div {
          margin-right: 12px; }
          .registration .imagesBlock .description a div div svg path {
            fill: var(--green); }
        .registration .imagesBlock .description a:hover {
          color: var(--blueDark); }
          .registration .imagesBlock .description a:hover div div svg path {
            fill: var(--blueDark); }

@media screen and (max-width: 1024px) {
  .registration .imagesBlock {
    display: none; } }
@media screen and (max-width: 1500px) {
  .registration .registrationForm {
    padding: 35px 35px; } }
