.myLevelCardBody {
  background: rgba(35, 188, 250, 0.1);
  border-radius: 10px;
  display: flex;
  margin-top: 24px;
  width: 100%;
  position: relative; }
  .myLevelCardBody_leftBlock {
    padding: 21px 0 21px 20px;
    background: linear-gradient(263.72deg, #086E98 0%, rgba(8, 110, 152, 0) 100%); }
    .myLevelCardBody_leftBlock .subTitle {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: var(--white);
      display: block;
      width: 105%; }
  .myLevelCardBody_rightBlock {
    position: absolute;
    top: calc(50% - 12px);
    right: 16px; }
    .myLevelCardBody_rightBlock span {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
      color: var(--white); }
  .myLevelCardBody .border {
    border-radius: 10px; }
