.customSlider h1 {
  font-style: normal;
  font-weight: 600;
  font-size: 64px;
  line-height: 140%;
  color: #FBFBFB;
  margin-left: 80px;
  padding-bottom: 40px; }
.customSlider svg {
  width: 80%;
  height: 80%; }
.customSlider .slick-arrow {
  background: rgba(35, 188, 250, 0.1);
  border-radius: 15px;
  width: 50px;
  height: 50px;
  position: absolute;
  bottom: 0;
  top: 95%;
  left: 6%;
  z-index: 99; }
  .customSlider .slick-arrow:hover {
    background: rgba(35, 188, 250, 0.6); }
    .customSlider .slick-arrow:hover:before {
      color: #0B2738; }
  .customSlider .slick-arrow:before {
    font-family: sans-serif;
    display: block;
    margin: 0 auto;
    color: rgba(35, 188, 250, 0.6); }
.customSlider .slick-next {
  left: 11%; }
.customSlider .slick-dots {
  text-align: end;
  right: 5%;
  top: 93%; }
  .customSlider .slick-dots li {
    margin: 0 10px; }
