:root {
  --background: linear-gradient(180deg, #172342 0%, #172342 100%);
  --white: #FBFBFB;
  --black: #021725;
  --darkBlue: #213054;
  --greyLight: #96A3AB;
  --greyDark: #55626A;
  --gradienBlack: linear-gradient(161.3deg, #015D7B 0%, #052B43 50%, #4F3265 100%);
  --gradientGreen: linear-gradient(161.3deg, #017B7B 0%, #054343 50%, #324065 100%);
  --gradient: linear-gradient(60.16deg, #00B8B0 5.38%, #0795CF 142.65%);
  --yellow: linear-gradient(90deg, #F49A1A 0%, #FFDE44 101.36%);
  --blueDark: #23BCFA;
  --green: #39D2D1;
  --red: #E06A6A;
  --blueLight: #219DD5; }

.selectWrapper {
  position: relative;
  border-bottom: solid 1px rgba(255, 255, 255, 0.2); }
  .selectWrapper:before, .selectWrapper:after {
    content: '';
    display: inline-block;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    cursor: pointer; }
  .selectWrapper:before {
    width: 16px;
    height: 20px;
    background-image: url("../../assets/icons/iconLocation.svg");
    right: 0; }
  .selectWrapper:after {
    width: 10px;
    height: 10px;
    background-image: url("../../assets/icons/iconSelectArrow.svg");
    left: 70px;
    top: 5px;
    transform: rotate(180deg); }

.optionShow:after {
  transform: rotate(0deg); }

.select {
  border-radius: 16px;
  background-color: transparent; }

.selectedCountries {
  display: flex;
  align-items: center; }
  .selectedCountries .counter {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: var(--white); }

.countries {
  display: flex;
  align-items: center;
  background: #1A4A6F;
  padding: 10px 20px;
  cursor: pointer; }
  .countries:hover {
    background: var(--green); }

.flag {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%; }
