.authorization {
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: var(--background); }
  .authorization .slider {
    width: 70%; }
  .authorization .authorizationForm {
    width: 30%;
    height: 100vh;
    padding: 62px 80px 40px 80px;
    background: var(--gradienBlack);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 20px 0 0 20px; }
    .authorization .authorizationForm .authorizationFormWrapper {
      display: flex;
      flex-direction: column; }
      .authorization .authorizationForm .authorizationFormWrapper h1 {
        font-family: 'fontello';
        font-style: normal;
        font-weight: 600;
        font-size: 40px;
        line-height: 49px;
        color: var(--white); }
      .authorization .authorizationForm .authorizationFormWrapper span {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: rgba(251, 251, 251, 0.8); }
    .authorization .authorizationForm form {
      display: flex;
      flex-direction: column; }
      .authorization .authorizationForm form .formItem {
        display: flex;
        flex-direction: column;
        margin-top: 50px; }
        .authorization .authorizationForm form .formItem label {
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 22px;
          color: rgba(255, 255, 255, 0.6); }
      .authorization .authorizationForm form .footerForm {
        display: flex;
        align-items: center;
        margin-top: 30px; }
        .authorization .authorizationForm form .footerForm .forgotPass {
          display: flex;
          align-items: center;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 17px;
          color: var(--blueDark);
          margin-left: 30px; }
          .authorization .authorizationForm form .footerForm .forgotPass:hover {
            color: var(--green); }
          .authorization .authorizationForm form .footerForm .forgotPass:visited {
            color: #32AFB4; }
          .authorization .authorizationForm form .footerForm .forgotPass img {
            margin-right: 6px; }
  .authorization .authorizationFooter {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .authorization .authorizationFooter_rightBlock {
      display: flex;
      flex-direction: column; }
      .authorization .authorizationFooter_rightBlock img {
        margin-right: 8px; }
    .authorization .authorizationFooter span {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.6); }
    .authorization .authorizationFooter a {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
      color: var(--green);
      margin-top: 10px;
      display: flex; }
      .authorization .authorizationFooter a:hover {
        color: var(--blueDark); }
        .authorization .authorizationFooter a:hover .icon div svg:hover path {
          fill: var(--blueDark); }
      .authorization .authorizationFooter a .icon {
        margin-right: 6px; }
    .authorization .authorizationFooter .iconQuestions div svg {
      width: 40px;
      height: 40px;
      cursor: pointer; }
      .authorization .authorizationFooter .iconQuestions div svg path {
        fill: rgba(35, 188, 250, 0.6); }
      .authorization .authorizationFooter .iconQuestions div svg:hover path {
        fill: #23BCFA; }

@media screen and (max-width: 1700px) {
  .authorization .authorizationForm {
    padding: 44px 50px; }
    .authorization .authorizationForm .authorizationFormWrapper h1 {
      font-size: 32px;
      line-height: 39px; } }
@media screen and (max-width: 1500px) {
  .authorization .authorizationForm {
    padding: 35px 55px; }
    .authorization .authorizationForm form .footerForm {
      flex-direction: column; }
      .authorization .authorizationForm form .footerForm button {
        width: 100%;
        margin-bottom: 20px; } }
@media screen and (max-width: 1300px) {
  .authorization .authorizationForm {
    width: 40%;
    padding: 20px 30px; }
    .authorization .authorizationForm .authorizationFormWrapper h1 {
      font-size: 28px;
      line-height: 20px; } }
@media screen and (max-width: 1024px) {
  .authorization .authorizationForm {
    width: 100%;
    padding: 20px 296px; }
    .authorization .authorizationForm .authorizationFormWrapper span, .authorization .authorizationForm .authorizationFormWrapper h1 {
      text-align: center; }
  .authorization form .formItem label {
    text-align: center; }
  .authorization .slider {
    display: none; } }
@media screen and (max-width: 870px) {
  .authorization .authorizationForm {
    padding: 30px 100px; } }
@media screen and (max-width: 570px) {
  .authorization .authorizationForm {
    padding: 30px 20px; } }
