@import '../../assets/style/mixins';

.historyPage {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: var(--background);
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include onTablet {
    flex-direction: column;
  }

  @include onNotebook {
    flex-direction: row;
  }

  @include onFull {
    flex-direction: row;
  }

  .HistoryList {
    flex: 0 0 70%;
  }

  .Statistic {
    flex: 0 0 27%;
  }
}
